body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

h1,
h2 {
  font-family: "Oswald", cursive; }

h1 {
  text-transform: uppercase; }

h3,
h4,
h5,
h6 {
  font-family: "Open Sans", sans-serif; }

.btn {
  font-family: "Oswald", cursive; }

p,
input,
a,
label {
  font-family: "Open Sans", sans-serif; }

.grid-button-label {
  font-family: "Oswald", cursive;
  font-weight: 700;
  font-size: 1em; }
  @media screen and (min-width: 576px) {
    .grid-button-label {
      font-size: 1em; } }
  @media screen and (min-width: 768px) {
    .grid-button-label {
      font-size: 1.4em; } }

.App {
  font-family: "Open Sans", sans-serif; }

.bg {
  min-height: 100vh;
  background: url("/img/concrete_seamless.png"); }

.admin-login,
.judge-login {
  height: 100%;
  min-height: 100vh;
  width: 100%;
  background-color: white;
  color: #333;
  background: url("/img/concrete_seamless.png"); }

.logo {
  height: auto;
  max-height: 250px;
  max-width: 85%; }

.nav {
  background: url("/img/concrete_seamless.png");
  color: #333; }
  .nav .logo {
    max-height: 150px; }

a {
  color: #333; }
  a:hover, a:visited, a:active {
    color: #1a1a1a; }
  a.selected {
    color: #f08400;
    text-decoration: underline; }

.text-link {
  color: #00e5ff; }
  .text-link:hover, .text-link:visited, .text-link:active {
    color: #00b7cc; }

.section-header {
  margin-bottom: 1em; }

.spliter {
  border-right: 5px #ff8c00 solid; }

.btn {
  border: none;
  background-color: #ff8c00;
  font-family: "Open Sans", sans-serif; }
  .btn:hover {
    background-color: #e67e00;
    color: white; }
  .btn:visited {
    background-color: #ff8c00;
    color: white; }
  .btn:active {
    background-color: #663800;
    color: white; }
  .btn.btn-accent2 {
    background-color: #00e5ff; }
  .btn.btn-danger {
    background-color: #333;
    color: white; }
  .btn.btn-success {
    background-color: #00b7cc;
    color: white; }
  .btn.btn-sort {
    border: none; }
    .btn.btn-sort.sort-active:hover, .btn.btn-sort.sort-active:active, .btn.btn-sort.sort-active {
      border: 4px solid #d5ff80;
      background-color: #e6ffb3;
      color: #334d00; }
    .btn.btn-sort.sort-up::after {
      font-family: "Font Awesome 5 Free";
      content: "\F0D8";
      display: inline-block;
      padding-left: 10px;
      vertical-align: middle;
      font-weight: 900; }
    .btn.btn-sort.sort-down::after {
      font-family: "Font Awesome 5 Free";
      content: "   \F0D7";
      display: inline-block;
      padding-left: 10px;
      vertical-align: middle;
      font-weight: 900; }

.icon-button {
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  cursor: pointer; }
  .icon-button:hover {
    color: #ff8c00; }

.projectsListItem {
  background-color: #ffe8cc;
  border: 1px white solid;
  font-size: 0.75em;
  padding: 1em 0.2em; }
  .projectsListItem:nth-child(even) {
    background-color: #ffddb3; }
  .projectsListItem:hover {
    background-color: #ccfaff; }

.grid-cell-header {
  text-align: center; }

.grid-cell {
  text-align: center;
  border-right: 1px solid #ff8c00;
  font-weight: bolder; }
  .grid-cell:first-of-type {
    border-left: 1px solid #cc7000; }
  .grid-cell:last-of-type {
    border-right: none; }
  .grid-cell.grid-cell-complete {
    background-color: #ccff66; }
  .grid-cell:hover {
    background-color: #66efff;
    cursor: pointer; }

.missing-judge {
  border: red 1px solid; }

.judge-finals-options {
  -webkit-appearance: none;
  /*Removes default chrome and safari style*/
  -moz-appearance: none;
  font-size: 17px;
  width: 50px;
  overflow: visible;
  overflow-wrap: break-word;
  -moz-white-space: pre-wrap;
  -o-white-space: pre-wrap;
  white-space: pre-wrap; }

.judge-row {
  padding: 1em;
  border-bottom: 1px solid #ff8c00; }
  .judge-row .judge-col {
    border-right: 1px solid #ff8c00; }
    .judge-row .judge-col.edit-mode {
      border: 1px solid #aaff00; }
  .judge-row .judge-pwd {
    text-align: center; }
    .judge-row .judge-pwd.pwd {
      -webkit-filter: blur(4px);
              filter: blur(4px); }

.judge-dashboard {
  min-height: 100vh;
  background-color: white;
  color: #333;
  background: url("/img/concrete_seamless.png"); }

.evaluation-section {
  color: #88cc00; }

.evaluation-subsection {
  color: black;
  border-radius: 0.4em;
  border: 2px solid #ff8c00;
  margin-bottom: 0.5em; }
  .evaluation-subsection:nth-of-type(even) {
    background-color: #ffba66; }
  .evaluation-subsection:nth-of-type(odd) {
    background-color: #ffddb3; }
  .evaluation-subsection.isComplete {
    border: 2px solid #00e5ff; }

.criterion-notes {
  line-height: 0.3em !important; }

.criterion-check {
  color: #008999; }

.grid-button-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap; }

.grid-button-group {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  padding: 0; }

.grid-button-label {
  width: 2.3em;
  height: 2.3em;
  background-color: white;
  border: 2px solid #ff8c00;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 0.1em 0.1em; }
  @media screen and (min-width: 576px) {
    .grid-button-label {
      margin: 0.2em 0.2em;
      width: 3em;
      height: 3em; } }
  @media screen and (min-width: 768px) {
    .grid-button-label {
      margin: 0.1em 0.3em;
      width: 1.8em;
      height: 1.8em; } }
  .grid-button-label.active {
    background-color: #00e5ff;
    border: 2px solid #00b7cc;
    color: black; }

.grid-button {
  display: none; }

.period-row {
  border-radius: 0.4em; }
  .period-row:nth-of-type(even) {
    background-color: #ffba66; }
    .period-row:nth-of-type(even).is-complete {
      border: 4px solid #bbff33;
      background-color: #ccff66; }
  .period-row:nth-of-type(odd) {
    background-color: #ffddb3; }
    .period-row:nth-of-type(odd).is-complete {
      border: 4px solid #d5ff80;
      background-color: #e6ffb3; }

.ranking-row {
  border-radius: 0.4em; }
  .ranking-row:nth-of-type(even) {
    background-color: #ffba66; }
  .ranking-row:nth-of-type(odd) {
    background-color: #ffddb3; }
  .ranking-row:hover {
    background-color: #ccfaff; }

